#App {
    display: grid;
    background-image: url(../public/img/background.jpg);
    grid-template-areas:
    "actionFieldBG1 actionFieldBG2"

;
}

#actionFieldBG2 {
    z-index: 1;
    width: 618px;
    height: 918px;
    background-repeat: round;
    background-image: url(../public/img/actionField2.jpg);
}

#actionFieldBG1 {
    width: 618px;
    height: 918px;
    background-repeat: round;
    background-image: url(../public/img/actionField1.jpg);
}

#actionField1 {
    top: 20px;
    left: 25px;
    display: grid;
    grid-area: actionField1;
    position: relative;
    grid-column-gap: 10px;
    grid-row-gap: 30px;
    align-content: space-evenly;
    grid-template-columns: 29% 29% 40%;
    grid-template-areas:
    "actionCardNumber1 actionCardNumber2 clearBlock"
    "actionCardNumber3 actionCardNumber4 clearBlock"
    "actionCardNumber5 actionCardNumber6 clearBlock";
}


#actionField2 {
    right: -5px;
    top: 30px;
    display: grid;
    position: relative;
    grid-area: actionField2;
    grid-column-gap: 10px;
    grid-row-gap: 15px;
    grid-template-columns: 37% 29% 29%;
    grid-template-areas:
    "actionCardNumber7 actionCardNumber8 actionCardNumber11"
    "clearBlock actionCardNumber9 actionCardNumber12"
    "clearBlock actionCardNumber10 actionCardNumber13";
}

.clearBlock1 {
    align-content: center;
    grid-area: clearBlock;
    width: 200px;
    display: grid;
    grid-row-gap: 22px;
}

.clearBlock2 {
    align-content: center;
    grid-area: clearBlock;
    width: 200px;
    display: grid;
    justify-self: center;
    grid-row-gap: 25px;
}

.actionButtonClear {
    width: 190px;
    height: 120px;
    border-radius: 10px;
}

.actionCard {
    border-radius: 20px;
    width: 162px;
    height: 262px;

}

#actionCardNumber7 {
    grid-area: actionCardNumber7;
    justify-self: center;
}

